  --green-light: #e7efee;
  --text: #000;
  --cod-gray: #0a0a0a;
  --textlight: #6b6b6b;
  --green50: rgba(14, 106, 99, 0.5);
  --serenade: #fff8eb;
  --box-shadow: 0 4px 4px -4px rgba(30, 33, 44, 0.03),
      0 12px 10px -6px rgba(154, 156, 165, 0.04),
      0 30px 24px -10px rgba(154, 156, 165, 0.05),
      0 80px 80px -20px rgba(154, 156, 165, 0.08);
  --border-radius: 30px;
  --hero-slider-speed: 1500;
  --burger-width: 40px;
  --burger-height: 20px;
  --burger-line-height: 2px;
}

html,
body {
  box-sizing: border-box;
  height: 100%;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  display: block;
  text-decoration: none;
  color: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

address {
  font-style: normal;
}

body {
  font-size: 20px;
  color: var(--text);
  overflow-x: hidden;
  min-width: 100vw;
}

button {
  padding: 0;
  margin: 0;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  background-color: transparent;
  cursor: pointer;
  border: none;
}

::marker {
  margin-top: 10px;
}

.main {
  overflow-x: clip;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.overlay {
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(6px);
  z-index: 9;
  opacity: 0;
  visibility: hidden;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.overlay--active {
  opacity: 1;
  visibility: visible;
}

.dis-scroll {
  position: relative;
  overflow: hidden;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

