.container {
  max-width: 1320px;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0 auto;
  outline: 1px solid brown;
}

.header {
  padding-top: 23px;
  padding-bottom: 25px;
}
.header__top {
  display: flex;
  align-items: center;
}
.header__catalog-btn {
  margin-right: 26px;
}
.header__search-form {
  margin-right: 26px;
}
.header__actions {
  display: flex;
  gap: 14px;
}
.header__bottom {
  margin-top: 20px;
}

.catalog-menu__btn-main {
  position: relative;
}
.catalog-menu__ground {
  position: absolute;
  width: 53.33vw;
  padding: 41px 10px 28px 37px;
  top: 112px;
  left: 16px;
  border-radius: 14px;
  height: 403px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.25);
  background: var(--white);
  z-index: 5;
  opacity: 0;
  visibility: hidden;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.catalog-menu__list {
  width: 48%;
  height: 343px;
  padding-right: 23px;
  overflow-x: hidden;
  overflow-y: auto;
}
.catalog-menu__list::-webkit-scrollbar {
  width: 4px;
}
.catalog-menu__list::-webkit-scrollbar-thumb {
  background-color: #e0e0e0;
  border-radius: 9px;
}
.catalog-menu__list::-webkit-scrollbar-track {
  background-color: #e0e0e0;
  border-radius: 9px;
}
.catalog-menu .catalog-menu__ground.catalog-menu__ground--open {
  opacity: 1;
  visibility: visible;
}
.catalog-menu__item {
  position: relative;
}
.catalog-menu__item:not(:last-child) {
  margin-bottom: 26px;
}
.catalog-menu__svg {
  fill: currentColor;
}
.catalog-menu__btn-secondary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: left;
  width: 100%;
  color: var(--cod-gray);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.catalog-menu__btn-secondary--active {
  color: var(--red);
}

.dropdown {
  position: fixed;
  width: 22.7%;
  height: 343px;
  top: 147px;
  left: 31%;
  background: var(--white);
  opacity: 0;
  visibility: hidden;
  overflow-x: hidden;
  overflow-y: auto;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.dropdown::-webkit-scrollbar {
  width: 4px;
}
.dropdown::-webkit-scrollbar-thumb {
  background-color: #FF9E9E;
  border-radius: 9px;
}
.dropdown::-webkit-scrollbar-track {
  background-color: #e0e0e0;
  border-radius: 9px;
}
.dropdown--active {
  opacity: 1;
  visibility: visible;
}
.dropdown__list {
  padding: 6px;
  width: 100%;
  height: 100%;
}
.dropdown__item {
  margin-bottom: 26px;
}
.dropdown__item--mb {
  margin-bottom: 42px;
}
.dropdown__link {
  color: var(--text);
}
.dropdown__link--title {
  font-weight: 600;
  color: var(--cod-gray);
}

.receiving {
  display: flex;
  color: var(--white);
}
.receiving__main::before {
  display: inline-block;
  content: "";
  background-color: rgba(255, 169, 0, 0.7);
  width: 65px;
  height: 48px;
  background: url(../images/icons/geolocation.svg) center center/24px 24px no-repeat rgba(255, 169, 0, 0.7);
  border-radius: 16px 0 0 16px;
}
.receiving__secondary {
  width: 230px;
  height: 48px;
  padding-top: 6px;
  background: var(--yellow);
  border-radius: 0 16px 16px 0;
}
.receiving__text {
  display: block;
  font-size: 14px;
}
.receiving__title {
  display: block;
  font-size: 16px;
}

.hero {
  padding-top: 25px;
  padding-bottom: 55px;
}
.hero__inner {
  display: flex;
  gap: 24px;
}
.hero-slider {
  width: 934px;
}
.hero-slide {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 26px;
}
.hero__title {
  width: 488px;
  margin-bottom: 24px;
}
.hero__content {
  padding-top: 110px;
  padding-bottom: 87px;
  padding-left: 52px;
}
.hero-arrow {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  background-color: #fff8eb;
  border-radius: 100%;
  border: 1px solid #FFF1D6;
  z-index: 2;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .hero-arrow:hover {
    border: 1px solid rgba(255, 169, 0, 0.45);
    background-color: #ffe8bc;
  }
}
.hero-arrow--prev {
  left: 0;
}
.hero-arrow--next {
  right: 0;
}
.hero .swiper {
  margin: 0;
}
.hero .swiper-pagination {
  text-align: left;
}
.hero .swiper-horizontal > .swiper-pagination-bullets,
.hero .swiper-pagination-bullets.swiper-pagination-horizontal,
.hero .swiper-pagination-custom,
.hero .swiper-pagination-fraction {
  bottom: 35px;
  left: 52px;
}
.hero .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.hero .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 6px);
}
.hero .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  display: inline-block;
  border-radius: var(--swiper-pagination-bullet-border-radius, 50%);
  background: rgb(255, 255, 255);
  opacity: var(--swiper-pagination-bullet-inactive-opacity, 0.6);
}
.hero .swiper-pagination-bullet-active {
  border-radius: 43px;
  width: 32px;
  opacity: var(--swiper-pagination-bullet-opacity, 1);
  background: var(--white);
}
