  border-radius: 16px;
  background-color: var(--serenade);
  border: 1px solid rgba(255, 169, 0, 0.45);
  text-align: left;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.tabs-buttons__text {
  display: block;
  margin-top: 6px;
  font-size: 16px;
}

.mixitup-control-active {
  border: 1px solid var(--yellow);
  background: var(--yellow);
  color: var(--white);
}

.search {
  position: relative;
  width: 317px;
}
.search__input {
  width: 100%;
  padding: 15px 12px;
  font-size: 16px;
  color: var(--textlight);
  border: 1px solid #e1e1e1;
  border-radius: 16px;
  background-color: var(--white);
}
.search__btn {
  position: absolute;
  width: 24px;
  height: 24px;
  top: 50%;
  transform: translateY(-50%);
  right: 17px;
}

.modal {
  --transition-time: 0.3s;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  overflow-y: auto;
  overflow-x: hidden;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal__container {
  position: relative;
  width: 1260px;
  margin: 50px auto;
  display: none;
  vertical-align: middle;
  background-color: #fff;
  border-radius: 20px;
  cursor: default;
  text-align: left;
  height: 805px;
}

@media (max-width: 1024px) {
  .modal__container {
    width: 90%;
  }
}
.modal-content__left {
  width: 39%;
  padding: 33px;
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal__container.modal-open {
  display: inline-block;
}

.fade {
  opacity: 0;
  transition: opacity var(--transition-time);
}

.fade.animate-open {
  opacity: 1;
  transition: opacity var(--transition-time);
}

.fadeInUp {
  opacity: 0;
  transform: translateY(-100px);
  transition: opacity var(--transition-time), transform var(--transition-time);
}

.fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
  transition: opacity var(--transition-time), transform var(--transition-time);
}

.content {
  min-height: 200vh;
  height: 805px;
}

.disable-scroll {
  position: relative;
  overflow: hidden;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

.favorite {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #e1e1e1;
  border-radius: 16px;
  width: 50px;
  height: 48px;
  color: #e1e1e1;
  background-color: var(--white);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .favorite:hover {
    border: 1px solid var(--yellow);
    background-color: #fff8eb;
    color: #ffa900;
  }
}

.user {
  position: relative;
}
.user__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #e1e1e1;
  border-radius: 16px;
  width: 50px;
  height: 48px;
  color: #e1e1e1;
  background-color: var(--white);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .user__btn:hover {
    border: 1px solid var(--yellow);
    background-color: #fff8eb;
    color: #ffa900;
  }
}
.user__go-login {
  position: absolute;
  top: 84px;
  left: 50%;
  transform: translate(-50%, 0);
  opacity: 0;
  visibility: hidden;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.user:hover .user__go-login {
  visibility: visible;
  opacity: 1;
  z-index: 12;
}

.go-login {
  width: 310px;
  padding: 17px 23px 23px;
  border: 1px solid #7E7C7C;
  border-radius: 12px;
  background: var(--white);
  cursor: default;
}
.go-login__title {
  margin-bottom: 30px;
}
.go-login__list:not(:last-child) {
  margin-bottom: 42px;
}
.go-login__item {
  padding-left: 65px;
  font-size: 14px;
  color: var(--text);
  line-height: 1.5714285714;
  height: 44px;
}
.go-login__item:not(:last-child) {
  margin-bottom: 8px;
}
.go-login__item--sale {
  background: url(../images/icons/go-login-sale.svg) left center/45px 44px no-repeat;
}
.go-login__item--delivery {
  background: url(../images/icons/go-login-delivery.svg) left center/45px 44px no-repeat;
}
.go-login__item--stock {
  background: url(../images/icons/go-login-stock.svg) left center/45px 44px no-repeat;
}
.go-login .modal-overlay {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
}
.go-login .modal-login {
  background-color: #fff;
  width: 300px;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  display: none;
  outline: 1px solid red;
}
.go-login .modal-overlay--visible {
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease-in-out;
}
.go-login .modal--visible {
